
@charset "UTF-8";
body {
  padding: 0;
  margin: 0;
  font-family: 'PT Sans Caption', sans-serif;
  background: #000000; }

a {
  text-decoration: none; }

p {
  color: #ffffff; }

header {
  width: 1280px;
  margin: 0 auto; }
  header .top-header {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between; }
    header .top-header .logo {
      text-transform: uppercase;
      font-size: 40px;
      color: #FF4900;
      font-weight: bold; }
    header .top-header p {
      color: white;
      font-size: 18px; }
    header .top-header .call {
      border: 1px solid #FF4900;
      padding: 15px 20px;
      border-radius: 10px;
      font-size: 20px; }
  header .bottom-header nav {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around; }
    header .bottom-header nav a {
      color: #FF4900;
      font-size: 18px; }

main .first-block {
  width: 1180px;
  margin: 0 auto;
  height: 90vh;
  position: relative;
  text-align: center; }
  main .first-block h1 {
    color: white;
    font-size: 71px;
    font-weight: bold;
    text-transform: uppercase;
    font-family: 'Montserrat', sans-serif;
    position: absolute;
    top: 30%; }
    main .first-block h1 span {
      color: #FF4900; }
  main .first-block .arr-down {
    position: absolute;
    bottom: 50px;
    right: 50%; }

main .promo {
  width: 950px;
  margin: 0 auto;
  height: 100vh;
  display: flex;
  flex-direction: row;
  position: relative; }
  main .promo .promo__left-block {
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 30px; }
    main .promo .promo__left-block h3 {
      color: #ffffff;
      text-align: right;
      text-transform: uppercase;
      font-size: 48px;
      font-family: 'Montserrat', sans-serif;
      margin: 0;
      margin-top: 27px; }
      main .promo .promo__left-block h3 span {
        color: #FF4900; }
    main .promo .promo__left-block img {
      margin-top: 40px; }
  main .promo .promo__right-block {
    width: 50%;
    margin: 30px;
    display: flex;
    flex-direction: column;
    justify-content: center; }
    main .promo .promo__right-block p {
      line-height: 1.5; }
      main .promo .promo__right-block p span {
        color: #FF4900; }
  main .promo .arr-down {
    position: absolute;
    bottom: 50px;
    right: 50%; }

/* Меню бургер */
.menu {
  position: relative;
  display: flex;
  justify-content: flex-end;
  width: 40%; }

.menu__links-item {
  color: #FF4900;
  font-size: 18px;
  text-transform: uppercase;
  text-decoration: none;
  margin-right: 30px;
  cursor: pointer; }

.menu__links-item:hover {
  color: #ffffff; }

.menu__links {
  position: absolute;
  display: none;
  top: 5px;
  right: 30px;
  left: 0;
  z-index: 1000; }

.menu.menu_state_open .menu__links {
  display: flex;
  flex-direction: row; }

.menu__icon {
  display: inline-block;
  width: 45px;
  height: 35px;
  position: relative;
  cursor: pointer; }

.menu__icon span {
  display: block;
  position: absolute;
  height: 6px;
  width: 100%;
  background: #FF4900;
  border-radius: 9px;
  opacity: 1;
  left: 0;
  transform: rotate(0deg);
  transition: .35s ease-in-out; }

.menu__icon span:nth-child(1) {
  top: 0px; }

.menu__icon span:nth-child(2),
.menu__icon span:nth-child(3) {
  top: 13px; }

.menu__icon span:nth-child(4) {
  top: 26px; }

.menu.menu_state_open .menu__icon span:nth-child(1) {
  top: 18px;
  width: 0%;
  left: 50%; }

.menu.menu_state_open .menu__icon span:nth-child(2) {
  transform: rotate(45deg); }

.menu.menu_state_open .menu__icon span:nth-child(3) {
  transform: rotate(-45deg); }

.menu.menu_state_open .menu__icon span:nth-child(4) {
  top: 18px;
  width: 0%;
  left: 50%; }
